import {STORAGE_KEYS} from '../util/constants';
import {useStickyState} from '../util/hooks';
import {applyColorScheme} from '../util/util';
import React, {createContext, useEffect, useCallback} from 'react';

export const ThemeContext = createContext();

const ThemeProvider = ({children}) => {
	const [colorScheme, setColorScheme] = useStickyState(
		'system',
		STORAGE_KEYS.colorScheme
	);

	const setAndApplyColorScheme = useCallback(
		colorScheme => {
			setColorScheme(colorScheme);
			applyColorScheme(colorScheme);
		},
		[setColorScheme]
	);

	useEffect(() => {
		const root = window.document.documentElement;
		const initialColorScheme = root.style.getPropertyValue(
			'--initial-theme'
		);
		setAndApplyColorScheme(initialColorScheme);
	}, []);

	return (
		<ThemeContext.Provider
			value={{colorScheme, setColorScheme: setAndApplyColorScheme}}
		>
			{children}
		</ThemeContext.Provider>
	);
};

export default ThemeProvider;
